body{
    width: 100%;
    height: 100%;
    background-color: #FAFAFA;
}

.LoginBody{
    width: 100%;
    height: 100vh;
}

.LoginLayout{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.LoginBox{
    width: 350px;
    height: 560px;
    display: grid;
    grid-template-rows: 400px 70px 90px;
    row-gap: 10px;
}

.LoginInput{
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
    border: #dbdbdb 1px solid;
    display: grid;
    grid-template-rows: 160px 130px 110px;
}

.SigninInput{
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
    border: #dbdbdb 1px solid;
}

.DownloadInput{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.LogoImg{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.InputBox{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6px;
}

.InputBox input:nth-child(1){
    width: 258px;
    height: 36px;
    padding-left: 10px;
    background-color: #FAFAFA;
    border: #dbdbdb 1px solid;
    border-radius: 4px;
}

.InputBox input:nth-child(1):focus{
    outline: none;
    border-color: #acacac;
}

.InputBox input:nth-child(2){
    width: 258px;
    height: 36px;
    padding-left: 10px;
    background-color: #FAFAFA;
    border: #dbdbdb 1px solid;
    border-radius: 4px;
}

.InputBox input:nth-child(2):focus{
    outline: none;
    border-color: #acacac;
}

.InputBox input:nth-child(3){
    width: 274px;
    height: 30px;
    margin-top: 10px;
    background-color: #0095F6;
    border: none;
    border-radius: 4px;
    font-weight: 700;
    color: #FFFFFF;
    cursor: pointer;
}

.InputBox input:nth-child(3):disabled{
    opacity: 30%;
}

.InputBox input:nth-child(3):active{
    opacity: 80%;
}

.Or{
    width: 276px;
    height: 100%;
    display: flex;
    align-items: center;
}

.Or div:nth-child(1){
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
}

.Or div:nth-child(3){
    width: 100%;
    height: 1px;
    background-color: #dbdbdb;
}

.Or div:nth-child(2){
    width: 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Or div:nth-child(2) p{
    font-size: 14px;
    color: #6d6d6d;
}

.MissingPw{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Missing{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
}

.Missing p{
    color: #2b2b2b;
    font-size: 12px;
}

.SigninInput{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.SigninInput p span{
    color: #0095F6;
    font-weight: 600;
    cursor: pointer;
}

.SigninInput p span:active{
    opacity: 80%;
}

.SigninInput p{
    color: #2b2b2b;
    font-size: 14px;
}

.DownloadImg{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: auto auto;
}

.DownloadImg div{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.DownloadImg div img{
    width: 136px;
    height: 40px;
}

.DownloadInput p{
    color: #2b2b2b;
    font-size: 14px;
}